@import './colors.scss';
@import './values.scss';

.projects {
  min-height: 100vh;
  // padding: 5.8px 10px 2px 10px;
  margin: 20px auto;
  background-color: $primarybackground;

  .wrapper {
    max-width: 1080px;
    margin: 20px auto;
    padding: 10px 20px;
    position: relative;

    .projectsOpen {
      font-size: 2rem;
      font-weight: 600;
      margin-bottom: 2rem;
      color: $lightwhite;
      background: $primarylight;
      border-radius: 10px;
      width: fit-content;
      padding: 8px 10px;
      font-family: 'Ubuntu Mono', monospace;
      letter-spacing: 0.5px;
    }

    .projectsClose {
      font-size: 2rem;
      font-weight: 600;
      margin-top: 3rem;
      color: $lightwhite;
      background: $primarylight;
      border-radius: 10px;
      width: fit-content;
      padding: 8px 10px;
      font-family: 'Ubuntu Mono', monospace;
      letter-spacing: 0.5px;
    }

    .projects_content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      gap: 3.6rem;
    }
  }
}
